import axios from 'axios';
import moment from 'moment';
export default{
  state:{
    students:[]
  },
  getters:{
    students:state=>state.students

  },
  mutations:{
    alertStudent(state,data){
      state.students = data;
    }
  },
  actions:{
    findStudentByQuery(context,keys){
      axios.get('http://101.132.44.172:30003/student/query/'+keys).then(({data})=>{
        data.forEach((item,index)=>{
          item.birth = moment(item.birth).format('YYYY-MM-DD');
        });
        context.commit('alertStudent',data);
      }).catch((error)=>{
        console.log(error);
      });
    },
    findStudentById(context,id){
      axios.get('http://101.132.44.172:30003/student/findById',{params:{'id':id}}).then(({data})=>{
        data[0].birth = moment(data[0].birth).format('YYYY-MM-DD');
        context.commit('alertStudent',data);
      }).catch((error)=>{
        console.log(error);
      });
    },
    findAllStudent(context){
      axios.get('http://101.132.44.172:30003/student/findAll').then(({data})=>{
        data.forEach((item,index)=>{
          item.birth = moment(item.birth).format('YYYY-MM-DD');
        });
        context.commit('alertStudent',data);

      }).catch((error)=>{
        console.log(error);
      });
    },
    saveStudent(context,form){
      return new Promise((resolve,reject)=>{
        axios.post('http://101.132.44.172:30003/student/save',form).then((result)=>{
          context.dispatch('findAllStudent');
          resolve(result);
        }).catch((error)=>{
          reject(error);
        });
      });
    },

    updateStudent(context,form){
      return new Promise((resolve,reject)=>{
        axios.post('http://101.132.44.172:30003/student/update',form).then((result)=>{
          context.dispatch('findAllStudent');
          resolve(result);
        }).catch((error)=>{
          reject(error);
        });
      })
    },

    deleteStudent(context,id){
      return new Promise((resolve,reject)=>{
        axios.post('http://101.132.44.172:30003/student/batchDelete',id).then((result)=>{
          context.dispatch('findAllStudent');
          resolve(result);
        }).catch((error)=>{
          reject(error);
        });
      });
    }

  }
}



